import { Component, OnInit } from '@angular/core';
declare var THREE: any;

export class EnnemyService {
    get mesh() {
        const geom = new THREE.TetrahedronGeometry(8, 2);
        const mat = new THREE.MeshPhongMaterial({
            color: 0xf25346,
            shininess: 0,
            specular: 0xffffff,
            shading: THREE.FlatShading
        });
        const mesh = new THREE.Mesh(geom, mat);
        mesh.castShadow = true;
        const angle = 0;
        const dist = 0;
        return mesh;
    }
}
export class EnnemyMesh {
    geom = new THREE.TetrahedronGeometry(8, 2);
    mat = new THREE.MeshPhongMaterial({
        color: 0xf25346,
        shininess: 0,
        specular: 0xffffff,
        shading: THREE.FlatShading
    });
    mesh = new THREE.Mesh(this.geom, this.mat);
    angle = 0;
    dist = 0;

}
export class EnnemyUse {
    angle: Number = 0;
    dist: Number = 0;
    mesh: any;
    constructor(angle: Number, dist: Number, mesh: any) {
        this.angle = angle;
        this.dist = dist;
        this.mesh = mesh;
    }
}

export class EnnemiesHolder {
    mesh = new THREE.Object3D();
    ennemiesInUse: Array<any> = [];
    constructor(mesh: any, ennemiesInUse: Array<any>) {
        this.mesh = mesh;
        this.ennemiesInUse = ennemiesInUse;
    }
}



