import { Component, OnInit } from '@angular/core';
import { FlyGameService } from './fly-game.service';
import { GameModel, GameModelSet } from './fly-game.model';
import {
    SeaService, CloudService, SkyService, AirPlaneServive, EnnemiesHolder, EnnemyService,
    EnnemyUse, ParticleService, ParticleHolder, EnnemyMesh
} from './model';

declare var THREE: any;

@Component({
    selector: 'app-fly-game',
    templateUrl: './fly-game.component.html',
    styleUrls: ['./fly-game.component.scss'],
    providers: [FlyGameService,
        SeaService,
        CloudService,
        SkyService,
        AirPlaneServive,
        EnnemyService,
        ParticleService]
})
export class FlyGameComponent implements OnInit {

    constructor(public _fly: FlyGameService,
        public _sea: SeaService,
        public _cloud: CloudService,
        public _sky: SkyService,
        public _airplane: AirPlaneServive,
        public _ennemy: EnnemyService,
        public _particle: ParticleService) {
    }

    scene: any;
    camera: any;
    render: any;
    renderer: any;
    container: any;
    gameModelSet: any;
    gameModel: any;
    ennemiesPool: Array<any> = [];
    game = this._fly.Variables;
    deltaTime: any = 0;
    particlesPool: Array<any> = [];

    HEIGHT: number;
    WIDTH: number;
    mousePos = { x: 0, y: 0 };
    ngOnInit() {
        this.sceneSet();
        this.lightSet();
        this.modelSet();
        // this.createEnnemies();
        // this.createParticles();
        this.modelDraw();
        this.renderRun();
    }

    sceneSet() {
        // 场景
        this.HEIGHT = window.innerHeight;
        this.WIDTH = window.innerWidth;
        // scence
        this.scene = new THREE.Scene();
        this.scene.fog = new THREE.Fog(0xf7d9aa, 100, 950);

        // camera
        const aspectRatio = this.WIDTH / this.HEIGHT;
        const fieldOfView = 60;
        const nearPlane = 1;
        const farPlane = 10000;

        this.camera = new THREE.PerspectiveCamera(
            fieldOfView,
            aspectRatio,
            nearPlane,
            farPlane
        );
        this.camera.position.x = 0;
        this.camera.position.z = 200;
        this.camera.position.y = 100;

        // renderer
        this.renderer = new THREE.WebGLRenderer({
            alpha: true,
            antialias: true
        });
        this.renderer.setSize(this.WIDTH, this.HEIGHT);
        this.renderer.shadowMapEnabled = true;

        this.container = document.getElementById('world');
        this.container.appendChild(this.renderer.domElement);

        window.addEventListener('resize', () => {
            this.handleWindowResize();
        });
    }
    lightSet() {
        const hemisphereLight = new THREE.HemisphereLight(0xaaaaaa, 0x000000, .9);
        const shadowLight = new THREE.DirectionalLight(0xffffff, .9);
        const ambientLight = new THREE.AmbientLight(0xdc8874, .5);
        shadowLight.position.set(150, 350, 350);
        shadowLight.castShadow = true;
        shadowLight.shadowCameraLeft = -400;
        shadowLight.shadowCameraRight = 400;
        shadowLight.shadowCameraTop = 400;
        shadowLight.shadowCameraBottom = -400;
        shadowLight.shadowCameraNear = 1;
        shadowLight.shadowCameraFar = 1000;
        shadowLight.shadowMapSizeWidth = 2048;
        shadowLight.shadowMapSizeHeight = 2048;

        this.scene.add(hemisphereLight);
        this.scene.add(shadowLight);
    }
    handleWindowResize() {
        this.HEIGHT = window.innerHeight;
        this.WIDTH = window.innerWidth;
        this.renderer.setSize(this.WIDTH, this.HEIGHT);
        this.camera.aspect = this.WIDTH / this.HEIGHT;
        this.camera.updateProjectionMatrix();
    }

    handleMouseMove(event: any) {
        const tx = -1 + (event.clientX / this.WIDTH) * 2;
        const ty = 1 - (event.clientY / this.HEIGHT) * 2;
        this.mousePos = { x: tx, y: ty };

    }

    modelSet() {
        this.gameModelSet = new GameModelSet();
        this.gameModel = new GameModel();
        const waves = this._sea.waves;
        this.moveWaves(waves);
        this.gameModel.sea = this._sea.mesh;
        this.gameModel.sea.receiveShadow = true;
        this.gameModel.sky = this._sky.skyMesh;
        this.gameModel.airplane = this._airplane.mesh;
        this.gameModel.particle = this._particle.mesh;
    }

    spawnEnnemies() {
        const nEnnemies = this.game.level;
        this.gameModel.ennemy = new THREE.Object3D();
        for (let i = 0; i < nEnnemies; i++) {
            let ennemy: any = {};
            if (this.ennemiesPool.length) {
                ennemy = this.ennemiesPool.pop();
            } else {
                ennemy = this._ennemy.mesh;
            }
            const angle = - (i * 0.1);
            const distance = this.game.seaRadius + this.game.planeDefaultHeight + (-1 + Math.random() * 2) * (this.game.planeAmpHeight - 20);

            if (ennemy.position) {
                ennemy.position.y = Math.random() * 10;
                ennemy.position.x = Math.random();
                const ennemyUse = new EnnemyUse(angle, distance, ennemy);
                this.gameModel.ennemy.add(ennemy);
                this.gameModel.ennemyUse.push(ennemyUse);
            }
        }
        return this.gameModel.ennemy;
    }
    rotateEnnemies() {
        for (let i = 0; i < this.gameModel.ennemyUse.length; i++) {
            const ennemy = this.gameModel.ennemyUse[i];
            ennemy.angle += this.game.speed * this.deltaTime * this.game.ennemiesSpeed;

            if (ennemy.angle > Math.PI * 2) {
                ennemy.angle -= Math.PI * 2;
            }
            if (ennemy.mesh) {
                ennemy.mesh.position.y = - this.game.seaRadius + Math.sin(ennemy.angle) * ennemy.distance;
                ennemy.mesh.position.x = Math.cos(ennemy.angle) * ennemy.distance;
                ennemy.mesh.rotation.z += Math.random() * .1;
                ennemy.mesh.rotation.y += Math.random() * .1;
            }

            const diffPos = this.gameModel.airplane.position.clone().sub(ennemy.mesh.position.clone());
            const d = diffPos.length();
            if (d < this.game.ennemyDistanceTolerance) {
                this.spawnParticles(ennemy.mesh.position.clone(), 15, 0xf25346, 3);
                this.ennemiesPool.unshift(this.gameModel.ennemyUse.splice(i, 1)[0]);
                this.gameModel.ennemy.remove(ennemy.mesh);
                this.game.planeCollisionSpeedX = 100 * diffPos.x / d;
                this.game.planeCollisionSpeedY = 100 * diffPos.y / d;
                // this.ambientLight.intensity = 2;

                // this.removeEnergy();
                i--;
            } else if (ennemy.angle > Math.PI) {
                this.ennemiesPool.unshift(this.gameModel.ennemiesInUse.splice(i, 1)[0]);
                this.gameModel.remove(ennemy.mesh);
                i--;
            }
        }
    }

    explode(pos: any, color: any, scale: any) {
        const _p = this.gameModel.particle.parent;
        this.gameModel.particle.material.color = new THREE.Color(color);
        this.gameModel.particle.material.needsUpdate = true;
        this.gameModel.particle.scale.set(scale, scale, scale);
        const targetX = pos.x + (-1 + Math.random() * 2) * 50;
        const targetY = pos.y + (-1 + Math.random() * 2) * 50;
        const speed = .6 + Math.random() * .2;
        /*         TweenMax.to(this.gameModel.particle.rotation, speed, { x: Math.random() * 12, y: Math.random() * 12 });
                TweenMax.to(this.gameModel.particle.scale, speed, { x: .1, y: .1, z: .1 });
                TweenMax.to(this.gameModel.particle.position, speed, {
                    x: targetX, y: targetY, delay: Math.random() * .1, ease: Power2.easeOut, onComplete: function () {
                        if (_p){
                            _p.remove(this.gameModel.particle);
                        } ;
                        this.gameModel.particle.mesh.scale.set(1, 1, 1);
                        this.particlesPool.unshift(this.gameModel.particle);
                    }
                }); */
    }

    spawnParticles(pos: any, density: any, color: any, scale: any) {

        const nPArticles = density;
        for (let i = 0; i < nPArticles; i++) {
            let particle: any = {};
            if (this.particlesPool.length) {
                particle = this.particlesPool.pop();
            } else {
                particle = this._particle.mesh;
            }
            this.gameModel.particle.add(particle);
            particle.visible = true;
            particle.position.y = pos.y;
            particle.position.x = pos.x;
            this.explode(pos, color, scale);
        }
    }
    moveWaves(waves: any) {
        if (this.gameModel.sea) {
            const verts = this.gameModel.sea.geometry.vertices;
            const len = verts.length;
            for (let i = 0; i < len; i++) {
                const v = verts[i];
                const vprops = waves[i];
                v.x = vprops.x + Math.cos(vprops.ang) * vprops.amp;
                v.y = vprops.y + Math.sin(vprops.ang) * vprops.amp;
                vprops.ang += vprops.speed;
            }
            this.gameModel.sea.geometry.verticesNeedUpdate = true;
            this.gameModel.sea.rotation.z += .005;
        }
    }

    modelDraw() {
        this.gameModel.sea.position.y = -600;
        this.scene.add(this.gameModel.sea);

        this.gameModel.sky.position.y = -600;
        this.scene.add(this.gameModel.sky);

        this.gameModel.airplane.scale.set(.25, .25, .25);
        this.gameModel.airplane.position.y = 100;
        this.scene.add(this.gameModel.airplane);
    }

    createEnnemies() {
        const ennemys =  this.spawnEnnemies();
        for (let i = 0; i < 10; i++) {
            const ennemy = this._ennemy.mesh;
            this.ennemiesPool.push(ennemy);
        }
        const ennemiesHolder = new EnnemiesHolder(this.gameModel.ennemy, this.gameModel.ennemyUse);
        console.log(ennemys);
        this.scene.add(ennemys);
    }

    createParticles() {
        for (let i = 0; i < 10; i++) {
            const particle = this._particle.mesh;
            this.particlesPool.push(particle);
        }
        const particlesHolder = new ParticleHolder(this.gameModel.particle);
        this.scene.add(particlesHolder.mesh);
    }
    renderRun() {
        const waves = this._sea.waves;
        this.render = () => {
            window.requestAnimationFrame(this.render);
            this.updatePlane();
            // this.spawnEnnemies();
            this.gameModel.sea.rotation.z += .005;
            this.gameModel.sky.rotation.z += .01;
            this.moveWaves(waves);
            this.renderer.render(this.scene, this.camera);
        };
        this.render();
    }

    updatePlane() {

        const targetY = this.normalize(this.mousePos.y, -.75, .75, 25, 175);
        const targetX = this.normalize(this.mousePos.x, -.75, .75, -100, 100);
        this.gameModel.airplane.position.y += (targetY - this.gameModel.airplane.position.y) * 0.1;
        this.gameModel.airplane.rotation.z = (targetY - this.gameModel.airplane.position.y) * 0.0128;
        this.gameModel.airplane.rotation.x = (this.gameModel.airplane.position.y - targetY) * 0.0064;
        this.gameModel.airplane.children[5].rotation.x += 0.3;
    }

    normalize(v: any, vmin: any, vmax: any, tmin: any, tmax: any) {

        const nv = Math.max(Math.min(v, vmax), vmin);
        const dv = vmax - vmin;
        const pc = (nv - vmin) / dv;
        const dt = tmax - tmin;
        const tv = tmin + (pc * dt);
        return tv;

    }
}





